import React from 'react';
import { URL } from "../../utils/_consts"
import { Form, Button } from 'react-bootstrap';
import { connect } from 'react-redux';

class DownloadTemplate extends React.Component {
    state = {
        depot: null,
    }

    onSubmit = async (event) => {
        event.preventDefault()

        const { depot } = this.state

        window.open(`${URL}/depot/download/${parseInt(depot)}/`, "_blank")

    }


    render() {
        return (
            <div className='upload-form'>
                <h4>Download Template</h4>


                <Form onSubmit={this.onSubmit}>
                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Depot</Form.Label>
                        <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.setState({ depot: e.target.value })}>
                            <option>Select</option>
                            {this.props.depot_list.map((depot) =>
                                <option value={depot.id}>{depot.name}</option>
                            )}


                        </Form.Select>
                    </Form.Group>


                    <div>
                        <Button variant='primary' type='submit' size='sm'>Download</Button>
                    </div>

                </Form>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        depot_list: state.list_reducer.depot_list,
    };
};

const mapDispatchToProps = {

};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DownloadTemplate);
