import React from 'react';
import { Form, Row, Col, Button, Alert } from 'react-bootstrap';
import { connect } from 'react-redux';
import SpinnerComponent from "../utils/spinner"

import { SALES_CREATE_ACTION } from '../../redux/actions/actions';
import { sassNull } from 'sass';

class SaleCreateForm extends React.Component {
    state = {
        product: null,
        customer: null,
        truck: null,
        entry_no: null,
        order_no: null,
        lpo_no: null,
        depot: null,
        date: new Date(),
        vol_obs: null,
        vol_20: null,
        selling_price: null,
        products: [],
        paid: true,

        loading_date: null,
        amount_paid: null,
        seal_no: null,
        remarks: null,

        trucks: [],
        spinner: false,
        success: false,
        null_error: false,
        show_error: false,
        submit: false,
    }

    componentWillReceiveProps = async (nextProps) => {
        if (nextProps.sales_create_error !== null) {
            this.setState({ spinner: false, show_error: true })
        } else {
            this.setState({ spinner: false, success: true })
        }
    }

    handleCustomerSelect = (id) => {
        let customer = this.props.customers[parseInt(id)]
        this.setState({ customer: customer.id, trucks: customer.trucks })
    }

    onSubmit = async (e) => {
        e.preventDefault()
        const {
            product, lpo_no, customer, truck,
            depot, entry_no, order_no, date, vol_obs,
            vol_20, selling_price, paid, loading_date,
            seal_no, amount_paid, remarks } = this.state

        let items = [product, customer, truck, depot, order_no, date, vol_obs, selling_price]
        let null_error = false
        for (let i = 0; i < items.length; i++) {
            if (items[i] === null || items[i] === "" || items[i] === undefined) {
                null_error = true
            }
        }

        if (null_error) {
            this.setState({ null_error: true })
        } else {
            this.setState({ spinner: true, submit: true })
            const { sales_date } = this.props
            await this.props.SALES_CREATE_ACTION(
                sales_date.start_date, sales_date.end_date,
                product, lpo_no, customer, truck, depot,
                entry_no, order_no, date, vol_obs, vol_20, selling_price, paid,
                loading_date, seal_no, amount_paid, remarks
            )
        }

    }

    componentDidMount() {
        this.setState({ show_error: false })
    }

    render() {
        let { date, loading_date, trucks } = this.state
        const { depots, customers, products } = this.props
        trucks = trucks.concat(this.props.truck_list)

        return (
            <div className='upload-form'>

                {/* SPINNER */}
                {this.state.spinner ?
                    <SpinnerComponent spinner={this.state.spinner} /> : null}

                {/* FORM */}
                <h4 className='upload-title'>Fill the information</h4>
                <p style={{ color: "brown", fontSize: 12 }}>All field mark with * must be filled</p>
                {/* If successful */}
                {this.state.success && this.state.submit ?
                    <Alert variant={"success"} size="sm">
                        Entry Created successful
                    </Alert> : null}

                {/* Null error */}
                {this.state.null_error ?
                    <Alert variant={"danger"} size="sm">
                        All the fields must be field
                    </Alert> : null}
                {this.state.show_error ?
                    <Alert variant={"danger"} size="sm">
                        {this.props.sales_create_error}
                    </Alert> : null}



                <Form onSubmit={this.onSubmit}>
                    <Row style={{ marginTop: 0, height: 'auto' }}>
                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Date <span style={{ color: "brown" }}>*</span></Form.Label>
                                <Form.Control type="date" placeholder="Date" size='sm' value={date} onChange={(e) => this.setState({ date: e.target.value, submit: false })} />
                            </Form.Group>
                        </Col>

                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Loading Date</Form.Label>
                                <Form.Control type="date" placeholder="Date" size='sm' value={loading_date} onChange={(e) => this.setState({ submit: false, loading_date: e.target.value })} />
                            </Form.Group>
                        </Col>
                    </Row>
                    <Row style={{ marginTop: 0, height: 'auto' }}>
                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Depot <span style={{ color: "brown" }}>*</span></Form.Label>
                                <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.setState({ submit: false, depot: e.target.value })}>
                                    <option>Select</option>
                                    {depots.map((depot, idx) =>
                                        <option value={depot.id}>{depot.name}</option>
                                    )}


                                </Form.Select>
                            </Form.Group>
                        </Col>

                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Customer <span style={{ color: "brown" }}>*</span></Form.Label>
                                <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleCustomerSelect(e.target.value)}>
                                    <option>Select</option>
                                    {customers.map((customer, idx) =>
                                        <option value={idx}>{customer.name}</option>
                                    )}


                                </Form.Select>
                            </Form.Group>
                        </Col>

                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Truck <span style={{ color: "brown" }}>*</span></Form.Label>
                                <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.setState({ truck: e.target.value })}>
                                    <option>Select</option>
                                    {trucks.map((truck) =>
                                        <option value={truck.id}>{truck.plate_no} - {truck.driver}</option>
                                    )}
                                </Form.Select>
                            </Form.Group>
                        </Col>

                    </Row>
                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Product <span style={{ color: "brown" }}>*</span></Form.Label>
                        <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.setState({ product: e.target.value })}>
                            <option>Select</option>
                            {products.map((product) =>
                                <option value={product.id}>{product.name}</option>
                            )}


                        </Form.Select>
                    </Form.Group>
                    <h6 className='upload-form__details'>Details</h6>

                    <Row style={{ marginTop: 0, height: 'auto' }}>
                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Order No <span style={{ color: "brown" }}>*</span></Form.Label>
                                <Form.Control type="text" placeholder="Order No" size='sm' onChange={(e) => this.setState({ show_error: false, order_no: e.target.value })} />
                            </Form.Group>
                        </Col>
                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Lpo no</Form.Label>
                                <Form.Control type="text" placeholder="Lpo no" size='sm' onChange={(e) => this.setState({ lpo_no: e.target.value })} />
                            </Form.Group>
                        </Col>
                    </Row>
                    <Row style={{ marginTop: 0, height: 'auto' }}>

                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Entry no</Form.Label>
                                <Form.Control type="text" placeholder="Entry No" size='sm' onChange={(e) => this.setState({ entry_no: e.target.value })} />
                            </Form.Group>
                        </Col>
                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Seal no</Form.Label>
                                <Form.Control type="text" placeholder="Seal no" size='sm' onChange={(e) => this.setState({ seal_no: e.target.value })} />
                            </Form.Group>

                        </Col>
                    </Row>

                    <Row style={{ marginTop: 0, height: 'auto' }}>
                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Vol OBS <span style={{ color: "brown" }}>*</span></Form.Label>
                                <Form.Control type="text" placeholder="Vol OBS" size='sm' onChange={(e) => this.setState({ vol_obs: e.target.value })} />
                            </Form.Group>
                        </Col>
                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Vol@20</Form.Label>
                                <Form.Control type="text" placeholder="Vol@20" size='sm' onChange={(e) => this.setState({ vol_20: e.target.value })} />
                            </Form.Group>
                        </Col>
                    </Row>

                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Selling Price <span style={{ color: "brown" }}>*</span></Form.Label>
                        <Form.Control type="text" placeholder="Selling price" size='sm' onChange={(e) => this.setState({ selling_price: e.target.value })} />
                    </Form.Group>

                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Amount Paid</Form.Label>
                        <Form.Control type="text" placeholder="Amount Paid" size='sm' onChange={(e) => this.setState({ amount_paid: e.target.value })} />
                    </Form.Group>

                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Paid</Form.Label>
                        <div key={`inline-radio`} className="mb-3">

                            <Form.Check
                                inline
                                label="Yes"
                                name="group1"
                                type={"radio"}
                                id={`inline-radio-1`}
                                onClick={() => this.setState({ paid: true })}
                            />
                            <Form.Check
                                inline
                                label="No"
                                name="group1"
                                type={"radio"}
                                id={`inline-radio-2`}
                                onClick={() => this.setState({ paid: false })}
                            />

                        </div>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                        <Form.Label>Remarks</Form.Label>
                        <Form.Control as="textarea" rows={3} onChange={(e) => this.setState({ remarks: e.target.value })} />
                    </Form.Group>

                    <div align='center'>
                        <Button variant='primary' type='submit'>Upload</Button>
                    </div>

                </Form>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        depots: (state.list_reducer.depot_list ? state.list_reducer.depot_list : []),
        products: (state.list_reducer.product_list ? state.list_reducer.product_list : []),
        customers: (state.list_reducer.customer_list ? state.list_reducer.customer_list : []),
        truck_list: (state.list_reducer.truck_list ? state.list_reducer.truck_list : []),
        sales_list: (state.list_reducer.sales_list ? state.list_reducer.sales_list : []),
        sales_date: state.update_reducer.sales_date,
        sales_create_error: state.list_reducer.sales_create_error,
    };
};

const mapDispatchToProps = {
    SALES_CREATE_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SaleCreateForm);
