import React from 'react';
import { Form, Button, Alert } from 'react-bootstrap';
import { connect } from 'react-redux';
import { Triangle } from "react-loader-spinner"
import { CUSTOMER_CREATE_ACTION } from '../../redux/actions/actions';

class CustomerUpload extends React.Component {
    state = {
        name: "",
        location: "",
        spinner: false,
        success: false,
        null_error: false,
        show_errors: false,
        customer_list: this.props.customer_list,
    }

    static getDerivedStateFromProps = async (props, state) => {

        if (props.customer_list === undefined) {
            return null
        }

        else if (state.customer_list === undefined && props.customer_list !== undefined) {

            state.spinner = false
            state.success = true
            state.null_error = false
        }
        else if (props.customer_list.length > state.customer_list.length) {
            state.spinner = false
            state.success = true
            state.null_error = false
        }
        state.customer_list = props.customer_list
        return state
    }

    onSubmit = async (event) => {
        event.preventDefault()

        const { name, location } = this.state

        if (name === "" || location === "") {
            this.setState({ null_error: true })
        } else {
            this.setState({ spinner: true })
            await this.props.CUSTOMER_CREATE_ACTION(name, location)
        }

    }


    render() {
        return (
            <div className='upload-form'>
                {this.state.spinner ?
                    <div className="spinner">
                        <Triangle

                            color="#00BFFF"
                            height={100}
                            width={100}
                            timeout={4000} //3 secs
                            visible={this.state.spinner}

                        />
                    </div> : null}
                <h4>Create Customer</h4>

                {/* If successful */}
                {this.state.success ?
                    <Alert variant={"success"} size="sm">
                        Customer created successful
                    </Alert> : null}

                {/* IF null error */}
                {this.state.null_error ?
                    <Alert variant={"danger"} size="sm">
                        The fields cannot be empty
                    </Alert> : null}

                {/* IF errors */}
                {/* {this.state.show_errors ?
                    Object.keys(this.props.error).map((key, idx) =>
                        <Alert key={idx} variant="danger">{key}: {this.props.create_customer_error[key][0]}</Alert>
                    )
                    : null} */}


                <Form onSubmit={this.onSubmit} id="sub">
                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Customer name</Form.Label>
                        <Form.Control type="text" placeholder="Name" size='sm' value={this.state.name}
                            onChange={(e) => this.setState({ name: e.target.value, success: false, null_error: false, show_errors: false })} />
                    </Form.Group>

                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Code</Form.Label>
                        <Form.Control type="text" placeholder="Name" size='sm' value={this.state.location}
                            onChange={(e) => this.setState({ location: e.target.value, success: false, null_error: false, show_errors: false })} />

                    </Form.Group>

                    <div>
                        <Button variant='primary' type='submit' size='sm' id="add_customer">Create</Button>
                    </div>

                </Form>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        customer_list: state.list_reducer.customer_list,
        // error: (state.create_reducer.create_customer_error ? state.create_reducer.create_customer_error : null),
    };
};

const mapDispatchToProps = {
    CUSTOMER_CREATE_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(CustomerUpload);
