import React from 'react';
import { connect } from 'react-redux';
import { Form, Alert, Button } from 'react-bootstrap'
import { Triangle } from "react-loader-spinner"

import { UPLOAD_EXCEL_ACTION } from "../../redux/actions/actions"

class UploadExcel extends React.Component {

    state = {
        file: null,
        spinner: false,
        success: false,
        show_error: false,
        null_error: false,
        depot: null,
    }

    componentWillReceiveProps = async (nextProps) => {
        if (nextProps.upload_excel_error) {
            this.setState({ show_error: true, spinner: false })
        } else {
            this.setState({ spinner: false, success: true })
        }

    }

    onSubmit = async (event) => {
        event.preventDefault()
        const { file, depot } = this.state
        if (file === null || depot === null) {
            this.setState({ null_error: true })
        } else {
            this.setState({ spinner: true })
            await this.props.UPLOAD_EXCEL_ACTION(file, depot)
        }


    }

    render() {
        return (
            <div className='upload-form'>
                {this.state.spinner ?
                    <div className="spinner">
                        <Triangle

                            color="#00BFFF"
                            height={100}
                            width={100}
                            timeout={4000} //3 secs
                            visible={this.state.spinner}

                        />
                    </div> : null}
                <h5>Upload File</h5>

                {/* If successful */}
                {this.state.success ?
                    <Alert variant={"success"} size="sm">
                        File Uploaded successful
                    </Alert> : null}

                {/* If error */}
                {this.state.show_error ?
                    <Alert variant={"danger"} size="sm">
                        {this.props.upload_excel_error.message}
                    </Alert> : null}

                {/* If null */}
                {this.state.null_error ?
                    <Alert variant={"danger"} size="sm">
                        All the field must be filled
                    </Alert> : null}

                <Form onSubmit={this.onSubmit}>
                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Depot</Form.Label>
                        <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.setState({ depot: e.target.value })}>
                            <option>Select</option>
                            {this.props.depot_list.map((depot) =>
                                <option value={depot.id}>{depot.name}</option>
                            )}


                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>File</Form.Label>
                        <Form.Control
                            type="file"
                            accept=".xlsx, .xls, .csv"
                            placeholder="File"
                            size='sm'
                            onChange={(e) => this.setState({ file: e.target.files[0] })}
                        />
                    </Form.Group>

                    <div>
                        <Button
                            variant='primary'
                            type='submit'

                            value={this.state.file}
                            size='sm'
                        >
                            Upload
                        </Button>
                    </div>

                </Form>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        uploaded_excel: state.update_reducer.uploaded_excel,
        upload_excel_error: state.update_reducer.upload_excel_error,
        depot_list: state.list_reducer.depot_list,
    };
};

const mapDispatchToProps = {
    UPLOAD_EXCEL_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(UploadExcel);
