import React from 'react';
import { Form, Button, Alert, Row, Col } from 'react-bootstrap';
import { connect } from 'react-redux';
import { Triangle } from "react-loader-spinner"
import {
    TRUCK_CREATE_ACTION, UPDATE_GET_TRUCK_LIST_ACTION,
    UPDATE_GET_CUSTOMER_LIST_ACTION, UPDATE_TRUCK_LIST_ACTION
} from '../../redux/actions/actions';

class TruckUpload extends React.Component {
    state = {
        customer: null,
        customers: [],
        customer_index: null,
        plate_no: "",
        first_name: "",
        last_name: "",
        spinner: false,
        success: false,
        null_error: false,
        show_errors: false,
        customer_list: this.props.customer_list,
        truck_list: this.props.truck_list,
    }

    static getDerivedStateFromProps = async (props, state) => {
        console.log(props.truck_create)
        if (props.update_get_truck_list === false) {
            state.spinner = false
            state.null_error = false
            // state.success = true
            state.show_errors = false
        }

        if (props.truck_create !== undefined && props.truck_create !== null) {

            if (props.truck_create.plate_no === state.plate_no) {
                console.log("in if")
                state.success = true
                if (state.customer_index !== null) {
                    let customers = props.customers
                    customers[parseInt(state.customer_index)].trucks = [props.truck_create].concat(customers[parseInt(state.customer_index)].trucks)
                    props.UPDATE_GET_CUSTOMER_LIST_ACTION(customers)
                } else {
                    props.UPDATE_TRUCK_LIST_ACTION([props.truck_create].concat(props.truck_list))
                }
                state.plate_no = ""
                state.first_name = ""
                state.last_name = ""
            }
        }
        // if (state.customer_index !== null) {
        //     if (props.customers[parseInt(state.customer_index)].trucks.length > state.customers[parseInt(state.customer_index)].trucks.length) {
        //         state.success = true
        //         await props.TRUCK_CREATE_CUSTOMER_ID_ACTION(state.customer_index)
        //     } else {
        //         // state.success = false
        //     }
        // } else if (state.truck_list.length !== props.truck_list.length) {
        //     state.success = true
        // } else {
        //     // state.success = false
        // }
        state.customers = props.customers
        state.truck_list = props.truck_list
        return state
    }

    handleCustomerSelect = (idx) => {
        try {
            const customer = this.props.customers[idx]
            this.setState({ customer: customer.id, customer_index: idx })
        } catch (e) {
            this.setState({ customer: null })
        }

    }
    onSubmit = async (event) => {
        event.preventDefault()

        const { customer, plate_no, first_name, last_name } = this.state

        if (plate_no === "" || first_name == "" || last_name == "") {
            this.setState({ null_error: true })
        } else {
            this.setState({ spinner: true, success: false })
            await this.props.UPDATE_GET_TRUCK_LIST_ACTION()
            await this.props.TRUCK_CREATE_ACTION(customer, plate_no, first_name, last_name)
        }

    }

    componentDidMount() {
        this.setState({ success: false })
    }
    render() {
        return (
            <div className='upload-form'>
                {this.state.spinner ?
                    <div className="spinner">
                        <Triangle

                            color="#00BFFF"
                            height={100}
                            width={100}
                            timeout={4000} //3 secs
                            visible={this.state.spinner}

                        />
                    </div> : null}
                <h4>Add Truck</h4>

                {/* If successful */}
                {this.state.success ?
                    <Alert variant={"success"} size="sm">
                        Truck created successful
                    </Alert> : null}

                {/* IF null error */}
                {this.state.null_error ?
                    <Alert variant={"danger"} size="sm">
                        The fields cannot be empty
                    </Alert> : null}

                {/* IF errors */}
                {/* {this.state.show_errors ?
                    Object.keys(this.props.error).map((key, idx) =>
                        <Alert key={idx} variant="danger">{key}: {this.props.create_customer_error[key][0]}</Alert>
                    )
                    : null} */}

                <Form onSubmit={this.onSubmit} id="sub">
                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Customer</Form.Label>
                        <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleCustomerSelect(e.target.value)}>
                            <option>Select</option>
                            {this.props.customers.map((customer, idx) =>
                                <option value={idx}>{customer.name}</option>
                            )}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Plate No</Form.Label>
                        <Form.Control type="text" placeholder="Name" size='sm' value={this.state.plate_no}
                            onChange={(e) => this.setState({ plate_no: e.target.value, success: false, null_error: false, show_errors: false })} />
                    </Form.Group>
                    <h6 className='upload-form__details'>Driver</h6>
                    <Row>
                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>First Name</Form.Label>
                                <Form.Control type="text" placeholder="Name" size='sm' value={this.state.first_name}
                                    onChange={(e) => this.setState({ first_name: e.target.value, success: false, null_error: false, show_errors: false })} />

                            </Form.Group>
                        </Col>
                        <Col>
                            <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                                <Form.Label>Last Name</Form.Label>
                                <Form.Control type="text" placeholder="Name" size='sm' value={this.state.last_name}
                                    onChange={(e) => this.setState({ last_name: e.target.value, success: false, null_error: false, show_errors: false })} />

                            </Form.Group>
                        </Col>
                    </Row>


                    <div>
                        <Button variant='primary' type='submit' size='sm' id="add_customer">Add</Button>
                    </div>

                </Form>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        customers: state.list_reducer.customer_list,
        truck_list: state.list_reducer.truck_list,
        update_get_truck_list: state.list_reducer.update_get_truck_list,
        truck_create: state.list_reducer.truck_create,
        // error: (state.create_reducer.create_customer_error ? state.create_reducer.create_customer_error : null),
    };
};

const mapDispatchToProps = {
    TRUCK_CREATE_ACTION,
    UPDATE_GET_TRUCK_LIST_ACTION,
    UPDATE_GET_CUSTOMER_LIST_ACTION,
    UPDATE_TRUCK_LIST_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TruckUpload);
