import React from 'react';
import Popup from 'reactjs-popup';
import { connect } from 'react-redux';
import { MdOutlineAddCircle, MdCalendarToday, MdKeyboardArrowDown, MdCloudDownload, MdOpacity } from "react-icons/md";
import { SiMicrosoftexcel } from "react-icons/si";
import { GiMoneyStack } from "react-icons/gi";

import { DateRangePicker } from 'react-date-range';
import { Button, Form } from 'react-bootstrap'
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import 'reactjs-popup/dist/index.css';
import {
    UPDATE_DASHBOARD_METRIC_ACTION,
    UPDATE_SELECTION_DATES_ACTION,
    UPDATE_PRODUCT_SERIES_ACTION,
    GET_PRODUCT_SERIES_ACTION,
    GET_DEPOT_SERIES_ACTION,
    GET_DEPOT_PRODUCT_SERIES_ACTION,
} from "../../redux/actions/actions"

import SaleCreateForm from "../sales/create"
import CustomerUpload from '../customer/create';
import AddCustomer from '../customer/customer_add';

import UploadExcel from '../sales/upload';
import DownloadTemplate from './download_template';
import TruckUpload from '../customer/truck_create';

class InputComponent extends React.Component {
    state = {
        startDate: this.props.selection_dates.startDate,
        endDate: this.props.selection_dates.endDate,
        dashboard_metric: this.props.dashboard_metric,
        key: 'selection',
        data: null,
    }

    handleMetricChange = async (value) => {
        if (value === "revenue") {
            await this.props.UPDATE_DASHBOARD_METRIC_ACTION(true)
        } else {
            await this.props.UPDATE_DASHBOARD_METRIC_ACTION(false)
        }

    }
    handleSelect = async (ranges) => {
        let id = this.props.depot_list[parseInt(this.props.depot)].id

        this.setState({
            startDate: ranges.selection.startDate,
            endDate: ranges.selection.endDate
        })

        await this.props.UPDATE_SELECTION_DATES_ACTION({
            startDate: ranges.selection.startDate,
            endDate: ranges.selection.endDate
        })
        await this.props.GET_PRODUCT_SERIES_ACTION(ranges.selection.startDate, ranges.selection.endDate)
        await this.props.UPDATE_PRODUCT_SERIES_ACTION()
        await this.props.GET_DEPOT_SERIES_ACTION(id, ranges.selection.startDate, ranges.selection.endDate)
        await this.props.GET_DEPOT_PRODUCT_SERIES_ACTION(id, ranges.selection.startDate, ranges.selection.endDate)

    }

    close = (data) => {
        console.log(data)
        this.setState({ entry_data: data })
    }
    static getDerivedStateFromProps(props, state) {
        const { selection_dates, dashboard_metric } = props
        if (typeof (selection_dates.startDate === "string")) {
            state.startDate = new Date(`${selection_dates.startDate}`)
            state.endDate = new Date(`${selection_dates.endDate}`)
        } else {
            state.startDate = selection_dates.startDate
            state.endDate = selection_dates.endDate
        }
        state.dashboard_metric = dashboard_metric
        return state
    }

    render() {
        console.log(this.state.entry_data)
        return (
            <div className='buttons'>
                <Popup trigger={
                    <Button className='input-dates' size='sm'>
                        <div className='input-dates__input-display'>
                            <span><MdCalendarToday color='#D04D30' size={18} /></span>
                            {this.state.startDate.toDateString()} - {this.state.endDate.toDateString()}
                            <span>
                                <MdKeyboardArrowDown size={18} />
                            </span>
                        </div>

                    </Button>
                }
                    position="bottom"
                >
                    <DateRangePicker
                        onChange={this.handleSelect}
                        showSelectionPreview={true}
                        moveRangeOnFirstSelection={false}
                        months={2}
                        ranges={[this.state]}
                        direction="horizontal"
                        preventSnapRefocus={true}
                        calendarFocus="backwards"
                        className='input-date__date-range'
                    />
                </Popup>
                <div className='metrics'>
                    <Button
                        variant={this.state.dashboard_metric ? "info" : "secondary"}
                        size='sm'
                        className='metrics__add-button'
                        style={{ color: "black" }}
                        onClick={() => this.handleMetricChange("revenue")}
                    >
                        <GiMoneyStack color="green" size={20} />
                        Revenue
                    </Button>
                    <Button
                        variant={this.state.dashboard_metric === false ? "info" : "secondary"}
                        size='sm'
                        className='metrics__add-button'
                        style={{ color: "black" }}
                        onClick={() => this.handleMetricChange("quantities")}
                    >
                        <MdOpacity color="orange" size={20} />
                        Quantities
                    </Button>
                </div>
                <div className='form'>
                    <Popup
                        contentStyle={{ width: 300 }}
                        trigger={
                            <Button
                                variant="secondary"
                                size='sm'
                                className='form__add-button'
                                style={{ color: "black" }}
                            >
                                <MdCloudDownload color="green" size={20} />
                                Template
                            </Button>
                        }
                        position="bottom"
                    >
                        <DownloadTemplate />
                    </Popup>

                    <Popup
                        contentStyle={{ width: 300 }}
                        trigger={
                            <Button variant="secondary" size='sm' className='form__add-button' style={{ color: "black" }}>
                                <SiMicrosoftexcel color="green" size={20} />
                                Upload
                            </Button>
                        }
                        position="bottom center"

                    >
                        <UploadExcel />

                    </Popup>

                    <Popup
                        contentStyle={{ width: 300 }}
                        trigger={
                            <Button variant="info" size='sm' className='form__add-button'>
                                <MdOutlineAddCircle color="#e9e7ec" size={20} />
                                Customer
                            </Button>
                        }
                        position="bottom center"

                    >
                        {/* <AddCustomer /> */}
                        <CustomerUpload />

                    </Popup>

                    <Popup
                        contentStyle={{ width: 300 }}
                        trigger={
                            <Button variant="info" size='sm' className='form__add-button'>
                                <MdOutlineAddCircle color="#e9e7ec" size={20} />
                                Truck
                            </Button>
                        }
                        position="bottom center"

                    >
                        <TruckUpload />

                    </Popup>

                    <Popup
                        contentStyle={{ width: 700, minHeight: 400 }}
                        trigger={
                            <Button variant="primary" size='sm' className='form__add-button' onClick={() => this.setState({ add_order: true })}>
                                <MdOutlineAddCircle color="#e9e7ec" size={20} />
                                Add Order
                            </Button>
                        }
                        position="left top"
                        onClose={this.close}
                    >
                        <SaleCreateForm close={this.close} />

                    </Popup>
                </div>
            </div>

        )
    }
}

const mapStateToProps = state => {
    return {

        selection_dates: state.update_reducer.selection_dates,
        dashboard_metric: state.update_reducer.dashboard_metric,
        depot: state.update_reducer.depot,
        depot_list: state.list_reducer.depot_list,
    };
};

const mapDispatchToProps = {
    UPDATE_DASHBOARD_METRIC_ACTION,
    UPDATE_SELECTION_DATES_ACTION,
    GET_PRODUCT_SERIES_ACTION,
    GET_DEPOT_SERIES_ACTION,
    GET_DEPOT_PRODUCT_SERIES_ACTION,
    UPDATE_PRODUCT_SERIES_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(InputComponent);