import React from 'react';
import { Form, Button, Alert, Row, Col } from 'react-bootstrap';
import { connect } from 'react-redux';
import { Triangle } from "react-loader-spinner"
import { ADD_CUSTOMER_ACTION, ADD_CUSTOMER_REMOVE_ACTION } from '../../redux/actions/actions';

class CustomerAdd extends React.Component {
    state = {
        customer: null,
        depot: null,
        spinner: false,
        success: false,
        null_error: false,
        show_errors: false,
    }

    static getDerivedStateFromProps = (props, state) => {
        if (props.add_customer !== undefined) {
            if (props.add_customer.status === "success") {
                state.spinner = false
                state.success = true
                state.null_error = false
            }
            else {
                state.spinner = false
                state.show_errors = true
            }
        }
        return state
    }

    handleCustomerSelect = (id) => {
        this.setState({ customer: id })
    }
    handleDepotSelect = (id) => {
        this.setState({ depot: id })
    }
    onSubmit = async (event) => {
        event.preventDefault()

        const { depot, customer } = this.state

        if (depot === null || customer === null) {
            this.setState({ null_error: true })
        } else {
            this.setState({ spinner: true })
            await this.props.ADD_CUSTOMER_ACTION(depot, customer)
        }

    }
    componentDidMount = async () => {
        await this.props.ADD_CUSTOMER_REMOVE_ACTION()
    }

    render() {
        return (
            <div className='upload-form'>
                {this.state.spinner ?
                    <div className="spinner">
                        <Triangle

                            color="#00BFFF"
                            height={100}
                            width={100}
                            timeout={4000} //3 secs
                            visible={this.state.spinner}

                        />
                    </div> : null}
                <h4>Add Customer</h4>

                {/* If successful */}
                {this.state.success ?
                    <Alert variant={"success"} size="sm">
                        Customer Added successfull
                    </Alert> : null}

                {/* IF null error */}
                {this.state.null_error ?
                    <Alert variant={"danger"} size="sm">
                        The fields cannot be empty
                    </Alert> : null}

                {/* IF errors */}
                {this.state.show_errors ?

                    <Alert variant="danger">Something went wrong!</Alert>

                    : null}

                <Form onSubmit={this.onSubmit} id="sub">
                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Depot</Form.Label>
                        <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleDepotSelect(e.target.value)}>
                            <option>Select</option>
                            {this.props.depots.map((depot, idx) =>
                                <option value={depot.id}>{depot.name}</option>
                            )}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-2" controlId="exampleForm.ControlInput1">
                        <Form.Label>Customer</Form.Label>
                        <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleCustomerSelect(e.target.value)}>
                            <option>Select</option>
                            {this.props.customers.map((customer, idx) =>
                                <option value={customer.id}>{customer.name}</option>
                            )}
                        </Form.Select>
                    </Form.Group>



                    <div>
                        <Button variant='primary' type='submit' size='sm' id="add_customer">Add</Button>
                    </div>

                </Form>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        customers: state.list_reducer.customer_list,
        depots: state.list_reducer.depot_list,
        add_customer: state.update_reducer.add_customer,
        // error: (state.create_reducer.create_customer_error ? state.create_reducer.create_customer_error : null),
    };
};

const mapDispatchToProps = {
    ADD_CUSTOMER_ACTION,
    ADD_CUSTOMER_REMOVE_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(CustomerAdd);
